import React, { useState } from "react";
import { Link } from "react-router-dom";
import { styles } from "../styles";
import { navLinks } from "../constants";
import { logo, menu, close } from "../assets";

const Navbar = () => {
  const [active, setActive] = useState("");
  const [toggle, setToggle] = useState(false);
  return (
    <nav
      className={`${styles.paddingX} w-full flex items-center py-5 fixed top-0 z-20 bg-primary h-[5rem]`}
    >
      <div className="w-full flex justify-between items-center max-w-7xl mx-auto">
        <Link
          to="/"
          className="flex items-center gap-2"
          onClick={() => {
            setActive("");
            window.scrollTo(0, 0);
          }}
        >
          <img src={logo} alt="logo" className="w-9 h-9 object-contain" />
          <p className="text-white text-[18px] font-bold cursor-pointer whitespace-nowrap">
            Marc Joyce
          </p>
        </Link>
        <ul className="list-none hidden sm:flex flex-row gap-10">
          {navLinks.map(link => (
            <li
              key={link.id}
              className={`${active === link.title ? 'text-white' : 'text-secondary'} hover:text-white text-[18px] font-medium cursor-pointer`}
              onClick={() => {
                setActive(link.title);
              }}
            >
              <a href={`#${link.id}`}>{link.title}</a>
            </li>
          ))}
        </ul>
        <div className='sm:hidden flex flex-1 justify-end items-center'>
          <img src={menu} alt='menu' className='w-[28px] h-[28px] object-contain cursor-pointer'
            onClick={() => setToggle(true)} />
          <div className={`${!toggle ? 'hidden' : 'flex'} p-6 bg-primary absolute top-0 right-0 w-full h-screen z-100`}>
          <img src={close} alt='menu' className='right-0 mr-6 absolute w-[28px] h-[28px] object-contain cursor-pointer'
            onClick={() => setToggle(false)} />
          <ul className="w-full list-none flex items-center flex-col gap-4 my-auto">
          {navLinks.map(link => (
            <li
              key={link.id}
              className={`${active === link.title ? 'text-white' : 'text-secondary'} font-poppins font-medium cursor-pointer text-[2rem] mb-[2rem]`}
              onClick={() => {
                setActive(link.title)
                setToggle(prev => !prev);
              }}
            >
              <a href={`#${link.id}`}>{link.title}</a>
            </li>
          ))}
        </ul>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
